.container {
  max-width: 600px;
  width: 100%;
}
.title {
  margin: 0;
}
.subtitle {
  margin: 0;
}
.tabContainer {
  display: flex;
}

.ingredientsList {
  padding: 0;
  margin: 0;

  list-style: none;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 24px;
}
.scrollWrapper {
  composes: container;
  overflow-y: scroll;
  max-height: 716px;
}

.scrollWrapper::-webkit-scrollbar {
  width: 8px;
}
.scrollWrapper::-webkit-scrollbar-track {
  margin-bottom: 51px;
  background-color: var(--color-background-track);
}
.scrollWrapper::-webkit-scrollbar-thumb {
  background-color: var(--color-default);
  height: 492px;
  width: 100%;
}
